<script>
    import axios from "axios";

    let detectResults = []
    let recordText = "开始发现"
    let detectText = ""
    let leftDataList = [], rightDataList = [];

    $: showResults = detectResults.filter((r, i) => {
        return i > detectResults.length - 20
    });

    const onAudioProcess = (event) => {
        let audioBuffer = event.inputBuffer;
        let leftChannelData = audioBuffer.getChannelData(0),
            rightChannelData = audioBuffer.getChannelData(1);
        // 需要克隆一下
        leftDataList.push(leftChannelData.slice(0));
        rightDataList.push(rightChannelData.slice(0));
        recordText = "收集声音中 ..."
    }

    const formatDate=(date)=>{
        let dateStrArray = date.toLocaleDateString().split("/");
        let dateStr=dateStrArray[2]+"-"+dateStrArray[0]+"-"+dateStrArray[1];
        let timeStr = date.toLocaleTimeString().substr(0,8);
        return dateStr+" "+ timeStr
    }

    const mergeArray = (list) => {
        let length = list.length * list[0].length;
        let data = new Float32Array(length),
            offset = 0;
        for (let i = 0; i < list.length; i++) {
            data.set(list[i], offset);
            offset += list[i].length;
        }
        return data;
    }

    const interleaveLeftAndRight = (left, right) => {
        let totalLength = left.length + right.length;
        let data = new Float32Array(totalLength);
        for (let i = 0; i < left.length; i++) {
            let k = i * 2;
            data[k] = left[i];
            data[k + 1] = right[i];
        }
        return data;
    }

    const createWavFile = (audioData) => {
        const WAV_HEAD_SIZE = 44;
        let buffer = new ArrayBuffer(audioData.length * 2 + WAV_HEAD_SIZE),
            // 需要用一个view来操控buffer
            view = new DataView(buffer);
        // 写入wav头部信息
        // RIFF chunk descriptor/identifier
        writeUTFBytes(view, 0, 'RIFF');
        // RIFF chunk length
        view.setUint32(4, 44 + audioData.length * 2, true);
        // RIFF type
        writeUTFBytes(view, 8, 'WAVE');
        // format chunk identifier
        // FMT sub-chunk
        writeUTFBytes(view, 12, 'fmt ');
        // format chunk length
        view.setUint32(16, 16, true);
        // sample format (raw)
        view.setUint16(20, 1, true);
        // stereo (2 channels)
        view.setUint16(22, 2, true);
        // sample rate
        view.setUint32(24, 44100, true);
        // byte rate (sample rate * block align)
        view.setUint32(28, 44100 * 2, true);
        // block align (channel count * bytes per sample)
        view.setUint16(32, 2 * 2, true);
        // bits per sample
        view.setUint16(34, 16, true);
        // data sub-chunk
        // data chunk identifier
        writeUTFBytes(view, 36, 'data');
        // data chunk length
        view.setUint32(40, audioData.length * 2, true);
        // 写入wav头部，代码同上
        // 写入PCM数据
        let length = audioData.length;
        let index = 44;
        let volume = 1;
        for (let i = 0; i < length; i++) {
            view.setInt16(index, audioData[i] * (0x7FFF * volume), true);
            index += 2;
        }
        return buffer;
    }

    const writeUTFBytes = (view, offset, string) => {
        let lng = string.length;
        for (let i = 0; i < lng; i++) {
            view.setUint8(offset + i, string.charCodeAt(i));
        }
    }

    const sendRecord = () => {
        // 发送录音
        let leftData = mergeArray(leftDataList);
        let rightData = mergeArray(rightDataList);
        let allData = interleaveLeftAndRight(leftData, rightData);
        let wavBuffer = createWavFile(allData);
        postRecordRequest(wavBuffer);
        leftDataList = [];
        rightDataList = [];
    }

    const postRecordRequest = (arrayBuffer) => {
        let blob = new Blob([new Uint8Array(arrayBuffer)]);
        let formData = new FormData();
        formData.append("file", blob);
        detectText = "识别中 ..."
        axios.post('http://127.0.0.1:8000/files', formData, {
            headers: {
                'Access-Control-Allow-Origin': "*",
                'Content-Type': 'multipart/form-data'
            }
        }).then(res => {
            let dateTime = new Date()
            console.log("data", res.data);

            let newResult = res.data.text
                .flat()
                .filter((a, index) => !!a && index > 0)
                .map((str, i) => {
                    let joinedStr = str.replace(/\t/g, ",").replace(/;/g, ",")
                    const arr = joinedStr.split(",")
                    return {
                        dateTime,
                        start: arr[4],
                        end: arr[5],
                        low: arr[6],
                        high: arr[7],
                        species: arr[9],
                        confidence: Number(arr[10]),
                    }
                })
                .filter(r => r.species !== "Human")
                .filter(r => r.confidence > 0.5)
                .map((all, index) => ({...all, index: detectResults.length + index}))

            if(newResult.length>0){
                detectResults = [...detectResults, ...newResult]
                detectText = "发现新的"
            }else{
                detectText = "未发现新的"
            }

            console.log("---detectResults", detectResults);
        })
    }

    const beginRecord = (mediaStream) => {
        let audioContext = new (window.AudioContext || window.webkitAudioContext);
        let mediaNode = audioContext.createMediaStreamSource(mediaStream);
        let jsNode = createJSNode(audioContext);
        // 需要连到扬声器消费掉outputBuffer，process回调才能触发
        // 并且由于不给outputBuffer设置内容，所以扬声器不会播放出声音
        jsNode.connect(audioContext.destination);
        jsNode.onaudioprocess = onAudioProcess;

        mediaNode.connect(jsNode);
    }

    const createJSNode = (audioContext) => {
        const BUFFER_SIZE = 4096;
        const INPUT_CHANNEL_COUNT = 2;
        const OUTPUT_CHANNEL_COUNT = 2;
        // createJavaScriptNode已被废弃
        let creator = audioContext.createScriptProcessor || audioContext.createJavaScriptNode;
        creator = creator.bind(audioContext);
        return creator(BUFFER_SIZE,
            INPUT_CHANNEL_COUNT, OUTPUT_CHANNEL_COUNT);
    }

    const record = () => {
        detectText = "准备中"
        recordText = "等待授权中"
        console.log(window.navigator)
        window.navigator.mediaDevices.getUserMedia({
            audio: true
        }).then(mediaStream => {
            console.log(mediaStream);
            beginRecord(mediaStream);
        }).catch(err => {
            console.error(err);
        })
    }

    setInterval(function () {
        sendRecord();
    }, 10 * 1000);
    setTimeout(()=>{
        record();
    },1000)

</script>

<main>
    <div class="content">
        <div class="record-text">{recordText}</div>
        <p>{detectText}</p>
        {#each showResults as r}
            <div class="result-item">
                <span> {formatDate(r.dateTime)}</span>
                <span>{r.species}</span>
                <span> {Math.round(r.confidence* 10000)/100}% </span>
            </div>

        {/each}
    </div>
</main>

<style>
    main {
        color: #FFFFCC;
        height: 95vh;
        background: #009966;
        text-align: center;
        padding: 1em;
        max-width: 240px;
        margin: 0 auto;
    }

    .record-text {
        margin: 0 auto;
        background: #FF6600;
        border: #FF6600;
        border-radius: 10px;
        width: 200px;
        padding: 10px;
        line-height: 32px;
        font-size: 24px;
        color: white;
    }

    .content {
        height: 90vh;
    }

    .result-item span{
        width: 200px;
        display: inline-block;
        text-align: left;
    }

    @media (min-width: 640px) {
        main {
            max-width: none;
        }
    }
</style>
